import styled from 'styled-components'

const Heading = styled.h1`
  color: #26144A;
  font-family: 'Merriweather', serif;
`

const H3 = Heading.withComponent('h3').extend`
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 8px;
`

export default { H3 }



