import styled from 'styled-components'

const CallToAction = styled.div`
  color: white;
  height: 40px;
  padding: 0 25px;
  font-weight: 600;
  line-height: 40px;
  border-radius: 20px;
  background: #4F15FF;
  display: inline-block;
  margin-bottom: 80px;
`

export default CallToAction



