import styled from 'styled-components'

const sizes = {
  s: '15px',
  m: '20px',
}

const lineHeights = {
  s: '22px',
  m: '30px',
}

const Paragraph = styled.p`
  color: #635685;
  font-size: ${props => sizes[props.size]}
  line-height: ${props => lineHeights[props.size]}
  margin-bottom: 30px;
`

Paragraph.defaultProps = {
  size: 'm',
}

export default Paragraph



