import styled from 'styled-components'

const Link = styled.a`
  color: #5800ff;
  font-weight: 500;
  position: relative;
  text-decoration: none;

  &:hover {
    color: #4210a2;
  }

  &::after {
    bottom: 0;
    left: -2px;
    right: -2px;
    z-index: -1;
    content: '';
    height: 10px;
    opacity: 0.1;
    position: absolute;
    background: #5800ff;
  }
`

export default Link



