import React from 'react'
import anime from 'animejs'
import styled from 'styled-components'

const BlobWrapper = styled.div`
  z-index: -2;
  top: ${props => props.top};
  left: ${props => props.left};
  right: ${props => props.right};
  bottom: ${props => props.bottom};
  position: absolute;
`

class Blob extends React.Component {
  get shapes() {
    return this.props.shapes.reverse().map(shape => ({
      value: shape,
    }))
  }

  componentDidMount() {
    anime({
      targets: '.blob',
      d: this.shapes,
      easing: 'linear',
      duration: 20000,
      loop: true,
    })
  }

  render() {
    return (
      <BlobWrapper
        top={this.props.top}
        right={this.props.right}
        bottom={this.props.bottom}
        left={this.props.left}
      >
        <svg
          width={this.props.width || 800}
          height={this.props.height || 800}
          viewBox="0 0 500 500"
        >
          <path fill="#FBF9FF" className="blob" d={this.props.shapes[0]} />
        </svg>
      </BlobWrapper>
    )
  }
}

export default Blob



