import React from 'react'
import Link from 'gatsby-link'
import MediaQuery from 'react-responsive'
import Blob from '../components/Blob'
import Anchor from '../components/Link'
import Skills from '../components/Skills'
import Paragraph from '../components/Paragraph'
import CallToAction from '../components/CallToAction'

const IndexPage = () => (
  <div>
    <Paragraph>
      Thomas is a designer and web developer from Dublin, Ireland who is
      currently writing about himself in the third person. I work with companies
      to design, build and ship products that are both beautiful and easy to
      use. I have worked in a wide spectrum of positions throughout the last few
      years; from product design to backend engineering.
    </Paragraph>
    <Paragraph>
      Please reach out to me on{' '}
      <Anchor href="https://twitter.com/thomasauros" target="_blank">
        twitter
      </Anchor>{' '}
      or via <Anchor href="mailto:hello@thomascullen.io">email</Anchor> if you
      are interested in working together or just want to say hi!
    </Paragraph>
    <Paragraph>
      You can see the latest projects I'm working on{' '}
      <Anchor href="https://dribbble.com/thomascullen" target="_blank">
        here
      </Anchor>, read my code{' '}
      <Anchor href="https://github.com/thomascullen" target="_blank">
        here
      </Anchor>{' '}
      and get snippets of my thoughts{' '}
      <Anchor href="https://github.com/thomascullen" target="_blank">
        here
      </Anchor>.
    </Paragraph>
    <Skills />
    <MediaQuery maxWidth={800}>
      {isMobile => (
        <div>
          <Blob
            top={isMobile ? '-50px' : '-100px'}
            left={isMobile ? '-100px' : '-320px' }
            width={isMobile ? 450 : 800}
            height={isMobile ? 450 : 800}
            shapes={[
              'M171.30425,467.330126 C319.121439,512.399382 460.683594,479.773437 480.75,383.953125 C500.816406,288.132812 445.753906,18.8828125 221.03125,12.3125 C-3.69140625,5.7421875 13.7773438,113.789063 13.7773438,230.324219 C13.7773438,346.859375 23.4870602,422.26087 171.30425,467.330126 Z',
              'M243.094423,472.539063 C390.911613,517.608318 493.414062,379.804687 483.75,279.21875 C474.085938,178.632813 445.753906,18.8828125 221.03125,12.3125 C-3.69140625,5.7421875 -2.265625,155.140625 11.3085937,279.21875 C24.8828125,403.296875 95.2772337,427.469807 243.094423,472.539063 Z',
            ]}
          />
          <Blob
            top={isMobile ? '450px' : '400px'}
            right={isMobile ? '-180px' : '-200px' }
            width={isMobile ? 500 : 800}
            height={isMobile ? 500 : 800}
            shapes={[
              'M250,470.75 C397.81719,515.819256 505.507813,458.953125 488.144381,266.375041 C470.780949,73.7969579 486.179687,22.3007813 294.847656,11.5507813 C103.515625,0.80078125 70.4140625,171.699219 35.5859375,273.269531 C0.7578125,374.839844 102.18281,425.680744 250,470.75 Z',
              'M171.30425,467.330126 C319.121439,512.399382 467.8874,510.810028 488.144381,266.375041 C508.401361,21.940055 435.019531,-50.2851562 291.070312,60.4140625 C147.121094,171.113281 66.5429688,136.132812 31.7148438,237.703125 C-3.11328125,339.273437 23.4870602,422.26087 171.30425,467.330126 Z',
            ]}
          />
        </div>
      )}
    </MediaQuery>
  </div>
)

export default IndexPage



